@font-face {
    font-family: 'EuroStyle';
    src: url('./assets/fonts/EuroStyleNormal.eot');
    src: url('./assets/fonts/EuroStyleNormal.eot?#iefix') format('embedded-opentype'),
        url('./assets/fonts/EuroStyleNormal.woff2') format('woff2'),
        url('./assets/fonts/EuroStyleNormal.woff') format('woff'),
        url('./assets/fonts/EuroStyleNormal.ttf') format('truetype'),
        url('./assets/fonts/EuroStyleNormal.svg#EuroStyleNormal') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}


// ./assets/fonts/

* {
    box-sizing: border-box;
    font-family: 'EuroStyle';
    // overflow-y: hidden;
    // overflow-x: hidden;
}

@media screen and (max-device-width: 950px) {
    body {
        -webkit-text-size-adjust: none;
    }
}

html,
body {
    -webkit-text-size-adjust: none;
    height: 100%;

}

* {
    margin: 0;
    padding: 0;
}

@keyframes scrollingKeyframe {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.app-main {
    display: flex;
    overflow-x: hidden;

    .click-to-continue-comp-div {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 6;
        background-color: rgba(0, 0, 0, 0.8);
        cursor: pointer;
    }

    .phone-rotation-indication-div {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 7;
        background-color: #333;
    }

    .App-main-top-nav {
        position: fixed;
        width: 100%;
        height: 20px;
        z-index: 7;
        top: 0%;
        left: 0;
        // background-color:"#333"
    }

    .app-left-btn-nav {
        position: fixed;
        top: 45%;
        left: 0;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background-color: #333333;
        border: 2px solid #fff;
        z-index: 5;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0.3;
        cursor: pointer;

        .arrow-icon {
            font-size: 1.5rem;
        }

        p {
            font-size: 1rem;
            color: #fff;
            //--------disabling text selection-------
            -moz-user-select: none;
            /* firefox */
            -webkit-user-select: none;
            /* Safari */
            -ms-user-select: none;
            /* IE*/
            user-select: none;
            /* Standard syntax */
        }

        &:hover {
            opacity: 0.5;
            transition: 0.2s ease-in-out;
        }
    }

    .app-right-btn-nav {
        position: fixed;
        top: 45%;
        right: 0;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background-color: #333333;
        border: 2px solid #fff;
        z-index: 5;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0.3;
        cursor: pointer;

        .arrow-icon {
            font-size: 1.5rem;
        }

        p {
            font-size: 1rem;
            color: #ffffff;
            //--------disabling text selection-------
            -moz-user-select: none;
            /* firefox */
            -webkit-user-select: none;
            /* Safari */
            -ms-user-select: none;
            /* IE*/
            user-select: none;
            /* Standard syntax */
        }

        &:hover {
            opacity: 0.5;
            transition: 0.2s ease-in-out;
        }
    }
    .full-screen-btn {
        position: fixed;
        top: 5%;
        right: 2%;
        z-index: 5;
        width: 12vw;
        height: 3vw;
        border-radius: 0.6vw;
        backdrop-filter: blur(10px);
        // border: 0.1vw solid #C8E404;
        background: linear-gradient(152deg, rgba(0, 0, 0, 1) 19%, #0092CE 100%);
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 1px;
        // opacity: 0.4;
        cursor: pointer;
      }
}

